import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

import { SelectItem, Message } from 'primeng/primeng';
import { ConfirmationService } from 'primeng/primeng';//

import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { PostsManageBean, ProductQBean } from '../bean/sale-product.bean'
import { CreditCcmService } from 'app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.service';

@Component({
	selector: 'app-credit-sale-product-view',
	templateUrl: './sale-product-view.component.html',
	styleUrls: ['./sale-product-view.component.scss'],
	providers: [CreditCcmService, ConfirmationService, Commfunc]
})

export class SaleProductViewComponent implements OnInit {
	@Input() viewRelation_data: any;
	@Output() closeDialog = new EventEmitter();
	saleProductType: any;;//-
	productVaildStatus: any; // 
	code: any;
	PostsManageBean: PostsManageBean = new PostsManageBean();
	productQBean: ProductQBean = new ProductQBean();

	//
	all_title: string = '';
	select_1: SelectItem[] = [];//
	tabMesg: any = tableMessage;//
	//
	updateValue: any = [];
	//
	msgs: Message[] = [];
	//
	showModel: string;
	headerTitle: string
	display: boolean = false;
	isdisabled: boolean = true;
	select_line: any = [];//
	select_group: any = [];//
	product_base_arr: any = [];//
	line_value: any = [{
		productCategoryId: ''
	}];//
	group_value: any = [{
		productCategoryId: ''
	}];//
	product_base_value: any = [];//
	//
	cars_2: any;
	//
	total: string;

	showConfirmBtn = false;

	showAddBtn = true;

	selectedList = new Array<Object>();

	//
	first: number = 0;
	constructor(
		public creditCcmService: CreditCcmService,
		public confirmationService: ConfirmationService,
		public commfunc: Commfunc
	) {
		this.codeValues() //
		this.saleProductType = this.code['SaleProductType'];
	}

	ngOnInit() {
		this.productVaildStatus = [
			{"label": "", "value": "0"},
			{"label": "", "value": "1"},
			{"label": "", "value": "2"}
		];
		this.select_line = [
			{ label: '', value: " " },
		];
		this.select_group = [
			{ label: '', value: "" },
		];
		this.product_base_arr = [
			{ label: '', value: "" },
		];
		this.select_1 = [
			{ label: '', value: " " },
		];
		this.cars_2 = [
		]
		// this.line_query();
		this.first = 0;
		if (this.viewRelation_data && this.viewRelation_data.flag === 'viewrelationAdd') {
			this.showConfirmBtn = true;
			this.showAddBtn = false;
			this.queryViewLationProduct();
		} else {
			this.searchList();
		}

	}

	//
	querySecle() {
		this.productQBean.pageSize = 10;
		this.productQBean.pageNum = 1;
		this.first = 0;
		if (this.viewRelation_data && this.viewRelation_data.flag === 'viewrelationAdd') {
			this.queryViewLationProduct();
		} else {
			this.searchList();
		}
	}

	codeValues() {
		this.commfunc.codeValue();
		let ret = this.commfunc.codeDatas;
		return this.code = ret //code
	}
	//  @ add wx by 20181011
	queryViewLationProduct() {
		this.productQBean.productLineId = this.line_value.productCategoryId;
		this.productQBean.productGroupId = this.group_value.productCategoryId;
		if (this.product_base_value.length === 0) {
			this.productQBean.productTemplateId = ',1'
		} else {
			this.productQBean.productTemplateId = this.product_base_value.tplId + ',1';
		}
		this.creditCcmService.findProAvailableProductList(this.productQBean).subscribe(data => {
			if (data.returnCode.code == "success") {
				this.cars_2 = data.resultList;
				this.total = data.total;
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '', detail: '' });
		})
	}
	// 
	searchList() {
		// this.productQBean.productLineId = this.line_value.productCategoryId;
		// this.productQBean.productGroupId = this.group_value.productCategoryId;
		// if (this.product_base_value.length === 0) {
		// 	this.productQBean.productTemplateId = ',1'
		// } else {
		// 	this.productQBean.productTemplateId = this.product_base_value.tplId + ',1';
		// }
		this.creditCcmService.findAvailableProductList(this.productQBean).subscribe(data => {
			if (data.returnCode.code == "success") {
				this.cars_2 = data.resultList;
				this.total = data.total;
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '', detail: '' });
		})
	}
	// 
	basicDetails(item) {
		this.display = true;
		this.headerTitle = '';
		this.showModel = '1';
		this.updateValue = item.productId;
	}
	//
	paths(item) {	
		this.confirmationService.confirm({
			message: '',
			accept: () => {
				window.location.href=item.target.nextElementSibling.href;
			},
			reject: () => {
				this.msgs = [];
				this.msgs.push({ severity: 'info', summary: '', detail: "" });
			}
		});
	}
	// 
	vaildProduct(item) {
		this.confirmationService.confirm({
			message: '',
			accept: () => {
				this.creditCcmService.copyProductProcessIntoProductByCredit({ 'productId': item.productId }).subscribe(data => {
					if (data.returnCode.code == "success") {
						this.msgs = [];
						this.msgs.push({ severity: 'info', summary: '', detail: '' });
						this.searchList();
					} else {
						this.msgs = [];
						this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
					}
				}, error => {
					this.msgs = [];
					this.msgs.push({ severity: 'error', summary: '', detail: "" });
				})
			},
			reject: () => {
				this.msgs = [];
				this.msgs.push({ severity: 'info', summary: '', detail: "" });
			}
		});
	}

	detailsCall(param) {
		this.display = param;
	}
	//
	paginate(event) {
		//
		this.productQBean.pageSize = event.rows;
		//
		let currentPage = event.page + 1;
		this.productQBean.pageNum = currentPage;
		this.first = (currentPage - 1) * this.productQBean.pageSize;
		//
		if (this.viewRelation_data && this.viewRelation_data.flag === 'viewrelationAdd') {
			this.queryViewLationProduct();
		} else {
			this.searchList();
		}
		this.productQBean.pageNum = 1;
	}
	// 
	line_query() {
		this.productQBean = new ProductQBean();
		this.creditCcmService.productLinePull(this.productQBean).subscribe(data => {
			if (data.returnCode.code == "success") {
				data.productLineList.forEach(item => {
					let obj = {};
					obj['label'] = item.categoryName;
					obj['value'] = item;
					this.select_line.push(obj)
				});
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '', detail: "" });
		})
	}


	// 
	lineVelue() {
		this.select_group = [
			{ label: '', value: "" },
		];
		this.group_value = "";
		this.product_base_value = "";
		this.group_query()
	}
	// 
	group_query() {
		let parm = { productLineId: this.line_value.productCategoryId }
		this.creditCcmService.productGroupPull(parm).subscribe(data => {
			if (data.returnCode.code == "success") {
				data.productGroupList.forEach(item => {
					let obj = {};
					obj['label'] = item.categoryName;
					obj['value'] = item;
					this.select_group.push(obj)
				});
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '', detail: "" });
		})
	}
	// 
	groupChange() {
		this.product_base_value = "";
		//id
		let params = {
			groupId: this.group_value.productCategoryId
		}
		this.product_base_arr = [
			{ label: '', value: "" },
		];
		this.creditCcmService.pdGroupTemplateQuery(params).subscribe(data => {
			if (data.returnCode.code == "success") {
				let arr = [];
				data.groupTempList.forEach(item => {
					let obj = {};
					obj['label'] = item.tplName;
					obj['value'] = item;
					this.product_base_arr.push(obj)
				});
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '', detail: '' });
		})
	}
	// 
	resetClick() {
		this.line_value = '';
		this.group_value = '';
		this.product_base_value = '';
		this.productQBean = new ProductQBean();
	}
	//
	permissionCheck(id) {
		return this.commfunc.permissionCheck(id)
	}

	confirm() {
		if (!this.selectedList || this.selectedList.length > 1) {
		  this.msgs = [{ severity: 'info', summary: '', detail: '' }];
		  return;
		} else {
		  this.closeDialog.emit(this.selectedList);
		}
	}
}

